.sb-avatar {
  background-color: var(--sbt-theme-tertiary-color-30);
  display: inline-block;
  height: var(--sb-avatar-size);
  overflow: hidden;
  text-align: center;
  width: var(--sb-avatar-size);

  img {
    height: 100%;
    object-fit: cover;
    width: 100%;
  }
}

.sb-avatar__text {
  height: 100%;
  width: 100%;
}

.sb-avatar__text_content {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  width: 100%;
}

.sb-avatar_circle {
  border-radius: var(--sb-avatar-size);
}

.sb-avatar_rounded {
  border-radius: var(--sbt-border-radius-small);
}

.sb-avatar_text {
  font-size: var(--sb-avatar-text-font-size);
}

.sb-avatar_xs {
  --sb-avatar-size: var(--sb-avatar-size-xs, 32px);
  --sb-avatar-text-font-size: var(--sbt-font-size-15);
}

.sb-avatar_small {
  --sb-avatar-size: var(--sb-avatar-size-small, 40px);
  --sb-avatar-text-font-size: var(--sbt-font-size-20);
}

.sb-avatar_medium {
  --sb-avatar-size: var(--sb-avatar-size-medium, 48px);
  --sb-avatar-text-font-size: var(--sbt-font-size-25);
}

.sb-avatar_large {
  --sb-avatar-size: var(--sb-avatar-size-large, 72px);
  --sb-avatar-text-font-size: var(--sbt-font-size-35);
}

.sb-avatar_xl {
  --sb-avatar-size: var(--sb-avatar-size-xl, 120px);
  --sb-avatar-text-font-size: var(--sbt-font-size-50);
}

.sb-avatar_text {
  background: linear-gradient(
    180deg,
    var(--sbt-color-background) 0%,
    var(--sbt-theme-tertiary-color-30) 100%
  );
  border: 1px solid var(--sbt-theme-tertiary-color-30);
  color: var(--sbt-theme-color-70);
}
