//--sb-avatar-group-spacing
//--sb-avatar-group-border

.sb-avatar-group__avatar-container {
  display: inline-block;
  margin: 0 var(--sb-avatar-group-spacing);
  position: relative;
}

.sb-avatar-group_stacked {
  --sb-avatar-group-spacing: -7px;
}

.sb-avatar-group_small {
  --sb-avatar-group-spacing: 2px;
}

.sb-avatar-group_medium {
  --sb-avatar-group-spacing: 4px;
}

.sb-avatar-group_large {
  --sb-avatar-group-spacing: 8px;
}
